*{
  font-family: Arial, Helvetica, sans-serif !important;
}

html, body {
  height: 100%;
  margin: 0;
  background: #f9f9f9;
}

.Home {
  height: 95vh;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 1fr 8fr 1fr;
  grid-row-gap: 10px;
  padding: 10px;
  margin: 20px 20px;
}

.headers{
  font-size: 26px;
  font-size: calc(18px + 0.9vw);
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr;
  grid-row-gap: 10px;
}

.pics {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-column-gap: 12px;
}

.buttons {
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 10px;
}

.buttons > div {
  background-color: grey;
}

.shadow {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.6), 0 6px 20px 0 rgba(0, 0, 0, 0.29);
}

img {
  background-size: cover;
  width: 100%;
  height: 100%;
  background-position: center;
}

a {
  cursor: pointer;
}

p {
  font-size: 17px;
}

.blue{
  color: #002f5b;
  font-weight: bolder;
}

@media screen and (max-width: 1281px){
  .pics {
    height: 100%;
    width: 100%;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
  }
}


@media screen and (max-width: 468px){
  .headers{
    grid-row-gap: 5px;
  }

  .pics {
    height: 100%;
    width: 100%;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    grid-column-gap: 5px;
    grid-row-gap: 5px;
  }

  .buttons {
    height: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
    grid-row-gap: 5px;
  }
}